import { writable } from "../../_snowpack/pkg/svelte/store.js";

import { v4 as uuidv4 } from "../../_snowpack/pkg/uuid.js";

const createData = () => {
	const { subscribe, update, set } = writable({
		goal: 100,
		balance: 0,
		revenue: 0,
	});

	return {
		subscribe,
		set,
	};
};

let template = {
	title: "Título",
	number: 0,
	revenue: 0,
	percent: 0,
	hightlight: false,
	saving: [],
};

const createDetails = () => {
	const { subscribe, update, set } = writable([
		{
			id: "78907323-3151-4439-b9b1-6653b4cd84c1",
			title: "Criptos",
			number: 2_000,
			revenue: 2,
			percent: 0,
			hightlight: false,
			saving: [],
		},
		{
			id: "78907698-3151-4439-b9b1-6653b4cd84c1",
			title: "ETF + Plan de Pensiones",
			number: 3_000,
			revenue: 1,
			percent: 0,
			hightlight: false,
			saving: [],
		},
	]);

	const add = () => update((n) => (n = [...n, { ...template, id: uuidv4() }]));
	const remove = (id) => update((n) => (n = n.filter((ele) => ele.id !== id)));

	const change = (index, key, value) => set((n) => (n[index][key] = value));
	return {
		subscribe,
		update,
		add,
		set,
		remove,
		change,
	};
};

const data = createData();
const details = createDetails();

export { details, data };
